'use client';
import React, { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';
import { 
  fetchSelectedSchedules, 
  fetchAvailableSchedules, 
  addSchedule, 
  deleteSchedule 
} from "../../Services/classScheduleService";
import { fetchClasses } from "../../Services/classService";

const ConfirmationDialog = ({ isOpen, onConfirm, onCancel, message }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-gray-800 bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-lg p-6 w-96">
        <p className="text-sm text-gray-700 mb-4">{message}</p>
        <div className="flex justify-end gap-4">
          <button
            onClick={onCancel}
            className="px-4 py-2 bg-gray-300 text-gray-700 rounded-md text-sm hover:bg-gray-400"
          >
            Cancel
          </button>
          <button
            onClick={onConfirm}
            className="px-4 py-2 bg-red-600 text-white rounded-md text-sm hover:bg-red-700"
          >
            Confirm
          </button>
        </div>
      </div>
    </div>
  );
};

const ClassSchedulePage = () => {
  const [classes, setClasses] = useState([]);
  const [selectedClassId, setSelectedClassId] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedSchedules, setSelectedSchedules] = useState([]);
  const [availableSchedules, setAvailableSchedules] = useState([]);
  const [selectedScheduleId, setSelectedScheduleId] = useState("");
  const [actionStatus, setActionStatus] = useState({ type: null, message: null });
  const [confirmationDialog, setConfirmationDialog] = useState({
    isOpen: false,
    message: "",
    onConfirm: null,
  });
  const [searchTerm, setSearchTerm] = useState("");
  const [scheduleSearchTerm, setScheduleSearchTerm] = useState("");
  const [showAddSchedule, setShowAddSchedule] = useState(false);
  const router = useRouter();

  const toggleAddSchedule = () => {
    setShowAddSchedule(!showAddSchedule);
  };

  useEffect(() => {
    const loadClasses = async () => {
      try {
        setLoading(true);
        const allClasses = await fetchClasses();
        setClasses(allClasses || []);
      } catch (err) {
        console.error("Error fetching classes:", err);
        setError("Failed to load classes. Please try again.");
      } finally {
        setLoading(false);
      }
    };

    loadClasses();
  }, []);

  const fetchScheduleData = async (classId) => {
    if (!classId) return;
    
    try {
      setLoading(true);
      setError(null);
      
      const [selected, available] = await Promise.all([
        fetchSelectedSchedules(classId),
        fetchAvailableSchedules(classId)
      ]);
      
      setSelectedSchedules(selected || []);
      setAvailableSchedules(available || []);
    } catch (err) {
      console.error("Error fetching schedules:", err);
      setError("Failed to load schedules. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (selectedClassId) {
      fetchScheduleData(selectedClassId);
    }
  }, [selectedClassId]);

  const handleClassChange = (e) => {
    setSelectedClassId(e.target.value);
    setSelectedScheduleId("");
  };

  const handleAddSchedule = async () => {
    if (!selectedScheduleId) {
      setActionStatus({ 
        type: 'error', 
        message: 'Please select a schedule first' 
      });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
      return;
    }
    
    try {
      setActionStatus({ type: 'loading', message: 'Adding schedule...' });

      const selectedIndex = parseInt(selectedScheduleId);
      const selectedSchedule = availableSchedules[selectedIndex];

      if (!selectedSchedule) {
        console.error("Selected schedule not found at index:", selectedIndex);
        setActionStatus({ 
          type: 'error', 
          message: 'Failed to retrieve selected schedule details' 
        });
        return;
      }

      const newSchedule = {
        classId: parseInt(selectedClassId),
        dayName: selectedSchedule.dayName,
        startTime: selectedSchedule.startTime,
        endTime: selectedSchedule.endTime
      };
      
      await addSchedule(newSchedule);
      await fetchScheduleData(selectedClassId);
      setSelectedScheduleId("");
      setActionStatus({ 
        type: 'success', 
        message: 'Schedule added successfully' 
      });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
    } catch (err) {
      console.error("Error adding schedule:", err);
      setActionStatus({ 
        type: 'error', 
        message: 'Failed to add schedule. Please try again.' 
      });
    }
  };

  const handleAddSelectedSchedules = async () => {
    const selectedSchedulesToAdd = availableSchedules.filter(schedule => schedule.isSelected);
    if (selectedSchedulesToAdd.length === 0) {
      setActionStatus({ type: 'error', message: 'No schedules selected for addition' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
      return;
    }

    try {
      setActionStatus({ type: 'loading', message: 'Adding selected schedules...' });
      await Promise.all(
        selectedSchedulesToAdd.map(schedule =>
          addSchedule({
            classId: parseInt(selectedClassId),
            dayName: schedule.dayName,
            startTime: schedule.startTime,
            endTime: schedule.endTime,
          })
        )
      );
      await fetchScheduleData(selectedClassId);
      setActionStatus({ type: 'success', message: 'Selected schedules added successfully' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
    } catch (err) {
      console.error("Error adding selected schedules:", err);
      setActionStatus({ type: 'error', message: 'Failed to add selected schedules. Please try again.' });
    }
  };

  const handleAddAllSchedules = async () => {
    if (availableSchedules.length === 0) {
      setActionStatus({ type: 'error', message: 'No schedules available to add' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
      return;
    }

    try {
      setActionStatus({ type: 'loading', message: 'Adding all schedules...' });
      await Promise.all(
        availableSchedules.map(schedule =>
          addSchedule({
            classId: parseInt(selectedClassId),
            dayName: schedule.dayName,
            startTime: schedule.startTime,
            endTime: schedule.endTime,
          })
        )
      );
      await fetchScheduleData(selectedClassId);
      setActionStatus({ type: 'success', message: 'All schedules added successfully' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
    } catch (err) {
      console.error("Error adding all schedules:", err);
      setActionStatus({ type: 'error', message: 'Failed to add all schedules. Please try again.' });
    }
  };

  const handleDeleteSchedule = async (schedule) => {
    setConfirmationDialog({
      isOpen: true,
      message: `Are you sure you want to delete the schedule for ${schedule.dayName} (${formatTime(schedule.startTime)} - ${formatTime(schedule.endTime)})?`,
      onConfirm: async () => {
        setConfirmationDialog({ isOpen: false, message: "", onConfirm: null });
        try {
          setActionStatus({ type: 'loading', message: 'Removing schedule...' });

          await deleteSchedule(schedule);
          await fetchScheduleData(selectedClassId);
          setActionStatus({ type: 'success', message: 'Schedule removed successfully' });
          setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
        } catch (err) {
          console.error("Error deleting schedule:", err);
          setActionStatus({ type: 'error', message: 'Failed to delete schedule. Please try again.' });
        }
      },
    });
  };

  const handleRemoveSelectedSchedules = async () => {
    const selectedSchedulesToRemove = selectedSchedules.filter(schedule => schedule.isSelected);
    if (selectedSchedulesToRemove.length === 0) {
      setActionStatus({ type: 'error', message: 'No schedules selected for removal' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
      return;
    }

    try {
      setActionStatus({ type: 'loading', message: 'Removing selected schedules...' });
      await Promise.all(selectedSchedulesToRemove.map(schedule => deleteSchedule(schedule)));
      await fetchScheduleData(selectedClassId);
      setActionStatus({ type: 'success', message: 'Selected schedules removed successfully' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
    } catch (err) {
      console.error("Error removing selected schedules:", err);
      setActionStatus({ type: 'error', message: 'Failed to remove selected schedules. Please try again.' });
    }
  };

  const handleRemoveAllSchedules = async () => {
    if (selectedSchedules.length === 0) {
      setActionStatus({ type: 'error', message: 'No schedules to remove' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
      return;
    }

    setConfirmationDialog({
      isOpen: true,
      message: 'Are you sure you want to remove all schedules?',
      onConfirm: async () => {
        setConfirmationDialog({ isOpen: false, message: "", onConfirm: null });
        try {
          setActionStatus({ type: 'loading', message: 'Removing all schedules...' });
          await Promise.all(selectedSchedules.map(schedule => deleteSchedule(schedule)));
          await fetchScheduleData(selectedClassId);
          setActionStatus({ type: 'success', message: 'All schedules removed successfully' });
          setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
        } catch (err) {
          console.error("Error removing all schedules:", err);
          setActionStatus({ type: 'error', message: 'Failed to remove all schedules. Please try again.' });
        }
      },
    });
  };

  const formatTime = (timeString) => {
    try {
      const [hours, minutes] = timeString.split(':');
      const hour = parseInt(hours);
      const period = hour >= 12 ? 'PM' : 'AM';
      const formattedHour = hour > 12 ? hour - 12 : (hour === 0 ? 12 : hour);
      return `${formattedHour}:${minutes} ${period}`;
    } catch (e) {
      return timeString;
    }
  };

  const filteredClasses = classes.filter(cls => 
    cls.classCode?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    cls.classSize?.toString().includes(searchTerm)
  );

  const filteredSchedules = selectedSchedules.filter(schedule => 
    schedule.dayName?.toLowerCase().includes(scheduleSearchTerm.toLowerCase()) ||
    formatTime(schedule.startTime)?.toLowerCase().includes(scheduleSearchTerm.toLowerCase()) ||
    formatTime(schedule.endTime)?.toLowerCase().includes(scheduleSearchTerm.toLowerCase())
  );

  return (
    <div className="p-4 sm:p-6 mx-auto bg-white min-h-screen w-full">
      <div className="mb-6 border-b pb-4">
        <h3 className="text-xl text-gray-900 font-bold">Class Schedule Management</h3>
        <p className="text-sm text-gray-600">
          Manage time slots for classes
        </p>
      </div>

      <div className="bg-gray-50 border rounded-md p-6 mb-6">
        <div className="mb-4">
          <h4 className="text-lg font-semibold text-gray-900 mb-4">Select a Class</h4>
          <div className="relative flex-grow">
            <input
              type="text"
              placeholder="Search classes..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full px-4 py-2 border rounded-md text-gray-700 bg-white text-xs focus:outline-none focus:ring-1 focus:ring-blue-500"
            />
            <div className="relative mt-2">
              <select
                value={selectedClassId}
                onChange={(e) => setSelectedClassId(e.target.value)}
                className="w-full px-4 py-2 border rounded-md text-gray-700 bg-white text-xs focus:outline-none focus:ring-1 focus:ring-blue-500"
              >
                <option value="">-- Select a Class --</option>
                {classes
                  .filter(cls => 
                    cls.classCode?.toLowerCase().includes(searchTerm.toLowerCase()) ||
                    cls.classSize?.toString().includes(searchTerm)
                  )
                  .map((cls) => (
                    <option key={cls.classId} value={cls.classId}>
                      {cls.classCode}
                    </option>
                  ))}
              </select>
            </div>
          </div>
        </div>
      </div>

      {error && (
        <div className="mb-6 p-4 bg-red-50 text-red-700 rounded-md border border-red-200 flex items-center">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2 flex-shrink-0" viewBox="0 0 20 20" fill="currentColor">
            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
          </svg>
          {error}
        </div>
      )}

      {actionStatus.message && (
        <div className={`mb-6 p-3 rounded-md text-sm flex items-center ${
          actionStatus.type === 'error' ? 'bg-red-50 text-red-700 border border-red-200' : 
          actionStatus.type === 'success' ? 'bg-green-50 text-green-700 border border-green-200' :
          'bg-blue-50 text-blue-700 border border-blue-200'
        }`}>
          {actionStatus.message}
        </div>
      )}

      {selectedClassId ? (
        loading ? (
          <div className="bg-gray-50 border rounded-md p-8 flex items-center justify-center">
            <div className="flex flex-col items-center">
              <svg className="animate-spin h-8 w-8 text-blue-500 mb-4" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              <p className="text-gray-700">Loading schedules...</p>
            </div>
          </div>
        ) : (
          <>
            <div className="bg-gray-50 border rounded-md p-6 mb-6">
              <div className="flex justify-between items-center mb-4">
                <h4 className="text-lg font-semibold text-gray-900">Add New Schedule</h4>
                <button
                  onClick={toggleAddSchedule}
                  className="px-3 py-1.5 bg-blue-600 text-white rounded-md text-xs hover:bg-blue-700 transition-colors"
                >
                  {showAddSchedule ? "Hide" : "Show"}
                </button>
              </div>
              {showAddSchedule && (
                <>
                  <div className="overflow-x-auto border border-gray-200 rounded-md shadow-sm mb-4">
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead>
                        <tr className="bg-gray-50">
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            <input
                              type="checkbox"
                              className="rounded text-blue-600"
                              onChange={(e) => {
                                const isChecked = e.target.checked;
                                const updatedSchedules = availableSchedules.map(schedule => ({
                                  ...schedule,
                                  isSelected: isChecked,
                                }));
                                setAvailableSchedules(updatedSchedules);
                              }}
                            />
                          </th>
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Day</th>
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Start Time</th>
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">End Time</th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-200">
                        {availableSchedules.length > 0 ? (
                          availableSchedules.map((schedule, index) => (
                            <tr key={`schedule-${index}`} className="hover:bg-gray-50">
                              <td className="px-4 py-2 whitespace-nowrap">
                                <input
                                  type="checkbox"
                                  className="rounded text-blue-600"
                                  checked={schedule.isSelected || false}
                                  onChange={(e) => {
                                    const updatedSchedules = [...availableSchedules];
                                    updatedSchedules[index] = {
                                      ...updatedSchedules[index],
                                      isSelected: e.target.checked,
                                    };
                                    setAvailableSchedules(updatedSchedules);
                                  }}
                                />
                              </td>
                              <td className="px-4 py-2 whitespace-nowrap text-sm font-medium text-gray-900">
                                {schedule.dayName}
                              </td>
                              <td className="px-4 py-2 whitespace-nowrap text-sm text-gray-700">
                                {formatTime(schedule.startTime)}
                              </td>
                              <td className="px-4 py-2 whitespace-nowrap text-sm text-gray-700">
                                {formatTime(schedule.endTime)}
                              </td>
                            </tr>
                          ))
                        ) : (
                          <tr>
                            <td colSpan="4" className="px-4 py-4 text-center text-sm text-gray-500">
                              No available schedules to add
                            </td>
                          </tr>
                        )}
                      </tbody>
                    </table>
                  </div>
                  <div className="flex flex-wrap gap-3 justify-end">
                    <button
                      onClick={handleAddSelectedSchedules}
                      className="px-3 py-1.5 bg-gray-200 text-gray-700 rounded-md text-xs hover:bg-gray-300 transition-colors flex items-center"
                    >
                      Add Selected
                    </button>
                    <button
                      onClick={handleAddAllSchedules}
                      className="px-3 py-1.5 bg-blue-600 text-white rounded-md text-xs hover:bg-blue-700 transition-colors flex items-center"
                    >
                      Add All
                    </button>
                  </div>
                </>
              )}
            </div>

            <div className="bg-gray-50 border rounded-md p-6">
              <div className="mb-4 flex flex-col md:flex-row justify-between items-start md:items-center">
                <h4 className="text-lg font-semibold text-gray-900 mb-2 md:mb-0">Current Schedules</h4>
                <div className="relative w-full md:w-64">
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <svg className="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd" />
                    </svg>
                  </div>
                  <input
                    type="text"
                    placeholder="Search schedules..."
                    value={scheduleSearchTerm}
                    onChange={(e) => setScheduleSearchTerm(e.target.value)}
                    className="pl-10 w-full px-4 py-2 border rounded-md text-gray-900 bg-white text-xs focus:outline-none focus:ring-1 focus:ring-blue-500"
                  />
                </div>
              </div>
              <div className="flex justify-end gap-3 mb-4">
                <button
                  onClick={handleRemoveSelectedSchedules}
                  className="px-3 py-1.5 bg-gray-200 text-gray-700 rounded-md text-xs hover:bg-gray-300 transition-colors flex items-center"
                >
                  Delete Selected
                </button>
                <button
                  onClick={handleRemoveAllSchedules}
                  className="px-3 py-1.5 bg-red-600 text-white rounded-md text-xs hover:bg-red-700 transition-colors flex items-center"
                >
                  Delete All
                </button>
              </div>

              {selectedSchedules.length > 0 ? (
                <>
                  <div className="overflow-x-auto border border-gray-200 rounded-md shadow-sm">
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead>
                        <tr className="bg-gray-50">
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            <input
                              type="checkbox"
                              className="rounded text-blue-600"
                              onChange={(e) => {
                                const isChecked = e.target.checked;
                                const updatedSchedules = selectedSchedules.map(schedule => ({
                                  ...schedule,
                                  isSelected: isChecked,
                                }));
                                setSelectedSchedules(updatedSchedules);
                              }}
                            />
                          </th>
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Day</th>
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Start Time</th>
                          <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">End Time</th>
                          <th className="px-4 py-2 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-200">
                        {filteredSchedules.length > 0 ? (
                          filteredSchedules.map((schedule, index) => (
                            <tr key={schedule.id || `${schedule.classId}-${schedule.dayName}-${schedule.startTime}`} 
                                className="hover:bg-gray-50">
                              <td className="px-4 py-2 whitespace-nowrap">
                                <input
                                  type="checkbox"
                                  className="rounded text-blue-600"
                                  checked={schedule.isSelected || false}
                                  onChange={(e) => {
                                    const updatedSchedules = [...selectedSchedules];
                                    const scheduleIndex = selectedSchedules.findIndex(s => 
                                      s.id === schedule.id || 
                                      (s.classId === schedule.classId && s.dayName === schedule.dayName && s.startTime === schedule.startTime)
                                    );
                                    if (scheduleIndex !== -1) {
                                      updatedSchedules[scheduleIndex] = {
                                        ...updatedSchedules[scheduleIndex],
                                        isSelected: e.target.checked,
                                      };
                                      setSelectedSchedules(updatedSchedules);
                                    }
                                  }}
                                />
                              </td>
                              <td className="px-4 py-2 whitespace-nowrap text-sm font-medium text-gray-900">
                                {schedule.dayName}
                              </td>
                              <td className="px-4 py-2 whitespace-nowrap text-sm text-gray-700">
                                {formatTime(schedule.startTime)}
                              </td>
                              <td className="px-4 py-2 whitespace-nowrap text-sm text-gray-700">
                                {formatTime(schedule.endTime)}
                              </td>
                              <td className="px-4 py-2 whitespace-nowrap text-right text-sm">
                                <button
                                  onClick={() => handleDeleteSchedule(schedule)}
                                  className="text-gray-500 hover:text-red-600 transition-colors"
                                  title="Remove schedule"
                                >
                                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                    <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                                  </svg>
                                </button>
                              </td>
                            </tr>
                          ))
                        ) : (
                          <tr>
                            <td colSpan="5" className="px-4 py-4 text-center text-sm text-gray-500">
                              No schedules match your search
                            </td>
                          </tr>
                        )}
                      </tbody>
                    </table>
                  </div>
                  <div className="mt-3 text-xs text-gray-600">
                    Showing {filteredSchedules.length} of {selectedSchedules.length} schedules
                  </div>
                </>
              ) : (
                <div className="text-center p-8 bg-white rounded-md border border-gray-200">
                  <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                  </svg>
                  <h3 className="mt-2 text-sm font-medium text-gray-900">No schedules</h3>
                  <p className="mt-1 text-sm text-gray-700">No schedules assigned to this class yet.</p>
                </div>
              )}
            </div>
          </>
        )
      ) : (
        <div className="text-center p-8 bg-white rounded-md border border-gray-200">
          <p className="mt-2 text-base text-gray-700">
            Please select a class from the dropdown above to view and manage its schedules.
          </p>
        </div>
      )}

      <ConfirmationDialog
        isOpen={confirmationDialog.isOpen}
        message={confirmationDialog.message}
        onConfirm={confirmationDialog.onConfirm}
        onCancel={() => setConfirmationDialog({ isOpen: false, message: "", onConfirm: null })}
      />
    </div>
  );
};

export default ClassSchedulePage;

